































































import Vue from 'vue';

export default Vue.extend({
    name: 'RomaFooter',
    props: {
        info: {
            type: Object
        },
        languages: {
            type: Array,
            default () {
                return [
                    {title: 'Español', short: 'es', icon: 'https://image.flaticon.com/icons/png/512/197/197593.png'}
                ]
            }
        }
    },
    data () {
        return {
            selectedLanguage: this.languages[0],
        }
    },
    computed: {
        contacts () {
            return [
                {icon: 'phone-volume', text: `Teléfono SAC: ${this.$store.state.info.contact.sac}`},
                {icon: 'mobile', text: `Teléfono Mobil: ${this.$store.state.info.contact.phone}`},
                {icon: 'at', text: `Correo Electrónico: ${this.$store.state.info.contact.email}`, url: `mailto://${this.$store.state.info.contact.email}`},
                // {icon: 'map-marked-alt', text: `Ver Ubicación`, url: `https://www.google.co.ve/maps/@${this.info.location.lat},${this.info.location.lon}`},
            ]
        },
        social () {
            return this.$store.state.info.social
        }
    }
});
